@import "../../styles/utils/variables.scss";

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__img {
    background-image: url("../../img/any/logo.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;

    &--header {
      width: 48px;
      height: 33px;
      margin-bottom: 4px;

      @media (min-width: $desktop) {
        margin-bottom: 6px;
      }
    }

    &--footer {
      width: 101px;
      height: 68px;
      margin-bottom: 8px;
    }
  }

  &__title {
    font-size: 16px;
    line-height: 19px;
    font-weight: 800;
    color: #fff;

    &--header {
      font-size: 10px;
      line-height: 12px;
    }
  }
}
